            {{   item.hanja }}  </span> 
          </div>   
      </div> 
        <div  class="row  " >   
            <button class="button-p" v-for="item in hList" :key="item.id"
            @click="clickH(item)">    
              {{ item.mean }} {{ item.sound }} 
          </button>  
      </div> 
    </div>
          <div  class="row card">  
              <div class="chip2" v-for="item in cardList" :key="item.id">  
                <span  
                 :class="['hanja', checkedHanja ? '' : 'notchecked']"
               >{{ item.hanja }} </span>
                 <span  
                  :class="['hangul', checkedHangul ? '' : 'notchecked']"
                >   {{ item.mean }} {{ item.sound }}
                  </span>  
                <!--span class="closebtn" onclick="this.parentElement.style.display='none'">&times;</span-->
              </div>  
          </div> 
      <div  class="row">
        <label class="check-container">한자
          <input type="checkbox"  v-model="checkedHanja">
          <span class="checkmark"></span>
        </label>
        <label class="check-container">한글
          <input type="checkbox" v-model="checkedHangul">
          <span class="checkmark"></span>
        </label>  
      </div> 

      <div class="row"> 
        <h3>index : {{index}} / {{size+1}}  ({{hanjaList.length}})</h3>
      </div>
    
      <div class="row">
        <button class="button button-card" @click="getHanjaWordRandom('prev');">&lsaquo;</button>   
        <button class="button button-card" @click="getHanjaWordRandom('next');">&rsaquo;</button>   
      </div> 
      
      <div class="row">
        <div class="search">    
          <input type="text" v-model="hanjaSearch">    <label>한자/한글</label>
          <button class="button" @click="getHanjaWord();">search</button>     
          <div   v-for="item in searchList" :key="item.hanja"> 
            <span class >{{ item.hanja }} ({{ item.mean }} {{ item.sound }})</span>
          </div>
          </div> 
      </div> 

        <div class="row">
