<script>  
import * as d3 from 'd3' 
export default {
  name: 'Hanja', 
  props: {
    msg: String
  },
   data() {
      return { 
        data: null,  
        checkedHanja: true,
        checkedHangul: true, 
        checkedHanjaTable: true,
        checkedHangulTable: true, 
        hanjaData: null,
        level: 5,
        columns: null, 
        index: 0,
        size: 0,
        searchList: [],
        cardList: [], 
        hanjaList: [], 
        pIndex: 0,
        pList: [],//문제
        hList: [],//해답
        hanjaSearch: "",
        hanjaSoundSearch: ""   
     }
    },
    mounted() {
      this.getLocal(); 
    },
    methods: {  
      clickH(item){
        console.log(item);
        let p = this.pList[this.pIndex];
        if (p.id === item.id) {
          console.log( "SU");
          this.pIndex ++;
        }
         
         if( this.pIndex >= 4) {
          this.getHanjaWordRandom('next');
         }
      },
      getHanjaWord() {
        var isHangel = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/; //한글
        
        const getHanjaLike = (arr, gubun, str) => arr.filter(d => d[gubun].indexOf(str) >= 0)
        const hanjaSearch = this.hanjaSearch

        if (hanjaSearch === "") {
          this.searchList = [];
          return;
        }

        let gubun = "hanja"
        if (isHangel.test(hanjaSearch)) {
          gubun = "sound"
        } 
        this.searchList = getHanjaLike(this.hanjaData, gubun, hanjaSearch)//"群");     
      },
      getHanjaWordRandom(arg){  
        if(arg === "prev") {
          this.index -= 2;
          //버튼 디저블 시키기
          if(this.index < 0) this.index = 0;
        }

        this.pIndex = 0;

        //this.cardList = this.getRandom(this.hanjaData, 4) 
         const index = this.index; 
         this.cardList = this.hanjaList.filter((_,i) => parseInt(i / 4) === index);
         
         this.pList = this.getRandom(this.cardList, 4)
         console.log(this.pList)
          this.hList = this.getRandom(this.cardList, 4)

        if(index < this.size ) this.index ++;
      },
      //랜덤 글자 가져오기 
      getRandom(arr, count = 1) {  
        if(count === 1) {
          return [arr[Math.floor(Math.random() * arr.length)]];
        }
        
        function shuffle(arg) { 
            let array = typeof arg === 'number' ?
              d3.range(arg).map(function(_, i) { return i; }) 
              : arg;
         
            array.sort(function() {
              return Math.random() - .5;
            });

            return array
          } 
         let indexList = shuffle(arr.length) 
      
        if(isNaN(count) || count === undefined || count.length === 0) {
          count = arr.length;
        }     

        return indexList.slice(0, count).map(d => arr[d])
      },
      changeLevel(){
        const getHanjaLike = (arr, gubun, str) => arr.filter(d => d[gubun].indexOf(str) >= 0)
        //  console.log(getHanjaLike(data, "sound", "구")) 
        
        //  let level = this.level;
        //  if(0 === parseInt(level)) this.hanjaList = data;//data.map(d => d)
        //  else 
        this.hanjaList = getHanjaLike(this.hanjaData, "level", this.level); 
        // this.hanjaList = getHanjaLike(this.hanjaList, "sound", "구"); 

        //this.hanjaList =  this.hanjaData
        this.hanjaList = this.getRandom(this.hanjaList, this.hanjaList.length)
        this.size = parseInt(this.hanjaList.length  / 4);
        this.index = 0;
        if (parseInt(this.hanjaList  % 4) > 0) this.size ++;
        this.getHanjaWordRandom();

        //this.searchList = getHanjaLike(this.hanjaData,  "sound", "구")
      },
      async getLocal() {      
        let columns;
        //var hangulList = new Set();
        //var isHangel = /[ㄱ|ㅏ-ㅣ|가-힣]/; //한글
        
        const data = await d3.csv("./hanja2.csv")  
        .then(function(d){
            //const letters = new Set(); 
            //console.log(d.columns) 
            //console.log(d)
            // d.map(e=>  hangulList.add(e.sound)) 
            // hangulList = Array.from(hangulList)
            columns = d.columns;
            return d; 
        })   
        //console.log(hangulList)
        // const aa = hangulList.filter(d=> isHangel.test(d.sound)) 
        // console.log(aa)  

        this.hanjaData = data;
        this.columns = columns;
        
        this.changeLevel()
      }
  } 
}
</script>

